[class*="col-"] {
    width: 100%;
}
  
@media only screen and (min-width: 768px) {
    /* For desktop: */
    .col-1 {width: 8.33%;}
    .col-2 {width: 16.66%;}
    .col-3 {width: 25%;}
    .col-4 {width: 33.33%;}
    .col-5 {width: 41.66%;}
    .col-6 {width: 50%;}
    .col-7 {width: 58.33%;}
    .col-8 {width: 66.66%;}
    .col-9 {width: 75%;}
    .col-10 {width: 83.33%;}
    .col-11 {width: 91.66%;}
    .col-12 {width: 100%;}
}


.container{
    display: grid;
    grid-template-columns: auto;
}
.container .sidemenu{
    margin: 10px;
}
.container .content{
    padding-top: 135px;
    margin: 10px 10px 10px 10px;
}
.top-toolbar{
    position: fixed;
    top:0px;
    width:100%;
    z-index: 10;
    background-color: var(--indigo-600) !important;
}
.list-title{
    position: fixed;
    top:80px;
    margin-right: -10px;
    width:calc(100% - 0px);
    z-index: 10;
    background-color: var(--indigo-600) !important;
    padding: 10px;
    font-size: 1.2rem;
    margin-bottom: 5px;
    border-radius: 0px;
    color: var(--primary-50);
    font-weight: bold;
}
.list-title-in-content{
    font-size: 1.2rem;
}
.tbl-caption{
    background-color: royalblue;
    color: #FFF;
    font-size: 1.2rem;
    padding: 10px;
}
.dialog-list-title{
    position: absolute;
    top:80px;
    margin-left: -10px;
    width:calc(100% - 40px);
    z-index: 10;
    background-color: var(--indigo-600) !important;
    padding: 10px;
    font-size: 1.2rem;
    margin-bottom: 5px;
    border-radius: 0px;
    color: var(--primary-50);
    font-weight: bold;
}
.dialog-list-title .p-toolbar .p-toolbar-group-start .p-button-group{
    display: block;
}
.list-cell-text-align{
    text-align: right;
}
